import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import visuallyHidden from '@mui/utils/visuallyHidden';
import { styled } from '@mui/material/styles';
import Chip from '@mui/material/Chip';
import { CAR_LIST } from '../constants';
import { Logo } from './Logo';

const StyledBox = styled('div')(({ theme }) => ({
  alignSelf: 'center',
  width: '100%',
  height: 400,
  marginTop: theme.spacing(8),
  borderRadius: (theme.vars || theme).shape.borderRadius,
  outline: '6px solid',
  outlineColor: 'hsla(220, 25%, 80%, 0.2)',
  border: '1px solid',
  borderColor: (theme.vars || theme).palette.grey[200],
  boxShadow: '0 0 12px 8px hsla(220, 25%, 80%, 0.2)',
  backgroundImage: `url(${process.env.TEMPLATE_IMAGE_URL || 'https://mui.com'}/static/screenshots/material-ui/getting-started/templates/dashboard.jpg)`,
  backgroundSize: 'cover',
  [theme.breakpoints.up('sm')]: {
    marginTop: theme.spacing(10),
    height: 700,
  },
  ...theme.applyStyles('dark', {
    boxShadow: '0 0 24px 12px hsla(210, 100%, 25%, 0.2)',
    backgroundImage: `url(${process.env.TEMPLATE_IMAGE_URL || 'https://mui.com'}/static/screenshots/material-ui/getting-started/templates/dashboard-dark.jpg)`,
    outlineColor: 'hsla(220, 20%, 42%, 0.1)',
    borderColor: (theme.vars || theme).palette.grey[700],
  }),
}));

const getModelColor = (model: string): 'default' => {
  return 'default';  // All models will have the same color as 
};

const getFloorColor = (floor: string) => {
  if (floor === '') return 'violet';
  return 'teal'; // Changed from 'success' to 'teal' for 5
};

const getAssignedColor = (assigned: string) => {
  if (assigned === 'ES') return 'pink';
  if (assigned === 'SS') return 'success';
  return 'default';
};

export default function Hero() {
  return (
    <Box
      id="hero"
      sx={(theme) => ({
        width: '100%',
        backgroundRepeat: 'no-repeat',
        backgroundImage:
          'radial-gradient(ellipse 80% 50% at 50% -20%, hsl(210, 100%, 90%), transparent)',
        ...theme.applyStyles('dark', {
          backgroundImage:
            'radial-gradient(ellipse 80% 50% at 50% -20%, hsl(210, 100%, 16%), transparent)',
        }),
      })}
    >
      <Container
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          pt: { xs: 4, sm: 8 },
          pb: { xs: 8, sm: 12 },
        }}
      >
        <Stack
          spacing={3}
          useFlexGap
          sx={{ alignItems: 'center', width: { xs: '100%', sm: '70%' } }}
        >
          <Typography
            variant="h1"
            sx={{
              display: 'flex',
              flexDirection: { xs: 'column', sm: 'row' },
              alignItems: 'center',
              fontSize: 'clamp(2rem, 8vw, 2.5rem)',
              mb: 2,
              fontFamily: 'HakgyoansimByeolbichhaneulTTF-B',
              fontWeight: 700,
            }}
          >
            
          </Typography>
          {CAR_LIST.map((car) => (
            <Stack 
              key={car.number}
              direction="row" 
              spacing={2} 
              alignItems="center"
              sx={{
                width: '100%',
                justifyContent: 'space-between',
                p: 2,
                borderRadius: 1,
                bgcolor: 'background.paper',
                boxShadow: 1,
              }}
            >
              <Stack direction="row" spacing={1} alignItems="center">
                <Chip 
                  label={car.model} 
                  color={getModelColor(car.model)} 
                  variant="outlined"
                  size="medium" 
                />
                {car.assigned && (
                  <Chip 
                    label={car.assigned} 
                    color={getAssignedColor(car.assigned)} 
                    variant="outlined"
                    size="medium"
                  />
                )}
                <Typography 
                  sx={{ 
                    fontSize: '1.2em', 
                    fontWeight: 500,
                    ml: 1
                  }}
                >
                  {car.number}
                </Typography>
              </Stack>
              <Chip 
                label={car.floor} 
                color={getFloorColor(car.floor)} 
                variant="outlined"
                size="medium" 
              />
            </Stack>
          ))}
          <Stack 
            sx={{ 
              mt: 2,
              display: 'flex',
              justifyContent: 'center',
              width: '100%'
            }}
            alignItems="center"
            gap={1}
          >
            <Logo />
            <Typography sx={{ fontSize: '0.8em', fontWeight: 500 }}>
            Copyright 2025 by HITECNSOL. All rights reserved
          </Typography>
          </Stack>

        </Stack>
      </Container>
    </Box>
  );
}
